"use client"

import React, { useEffect, useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { LucideIcon, Home, Settings, Users, BarChart2, MessageSquare, AlertCircle, LogOut, LogIn } from "lucide-react"
import { cn } from "../lib/utils"
import { OwnerLogin } from "./OwnerLogin"

interface NavItem {
  name: string
  url: string
  icon: LucideIcon
}

interface NavBarProps {
  items?: NavItem[]
  className?: string
}

const defaultItems: NavItem[] = [
  { name: "Dashboard", url: "/", icon: Home },
  { name: "Students", url: "/students", icon: Users },
  { name: "Analytics", url: "/analytics", icon: BarChart2 },
  { name: "Settings", url: "/settings", icon: Settings },
  { name: "Contact Support", url: "/support", icon: MessageSquare },
  { name: "Raise Complaint", url: "/complaint", icon: AlertCircle },
]

export function OwnerNavBar({ items = defaultItems, className }: NavBarProps) {
  const [activeTab, setActiveTab] = useState(items[0]?.name || "")
  const [isMobile, setIsMobile] = useState(false)
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [showLoginModal, setShowLoginModal] = useState(false)
  const router = useRouter()

  useEffect(() => {
    // Check if owner is logged in
    const ownerData = localStorage.getItem('ownerData')
    setIsLoggedIn(!!ownerData)

    const handleResize = () => {
      setIsMobile(window.innerWidth < 768)
    }

    handleResize()
    window.addEventListener("resize", handleResize)
    return () => window.removeEventListener("resize", handleResize)
  }, [])

  const handleLogout = () => {
    localStorage.removeItem('ownerData')
    setIsLoggedIn(false)
    router.push('/')
  }

  const handleLoginSuccess = () => {
    setIsLoggedIn(true)
    setShowLoginModal(false)
  }

  return (
    <>
      <div
        className={cn(
          "fixed bottom-0 sm:top-0 left-1/2 -translate-x-1/2 z-50 mb-6 sm:pt-6",
          className,
        )}
      >
        <div className="flex items-center gap-3 bg-background/5 border border-border backdrop-blur-lg py-1 px-1 rounded-full shadow-lg">
          {items.map((item) => (
            <Link
              key={item.name}
              href={item.url}
              className={cn(
                "flex items-center gap-2 px-4 py-2 text-sm font-semibold text-primary hover:bg-primary/5 rounded-full transition-colors",
                activeTab === item.name && "bg-primary/10"
              )}
              onClick={() => setActiveTab(item.name)}
            >
              <item.icon size={18} strokeWidth={2.5} />
              <span className="hidden md:inline">{item.name}</span>
            </Link>
          ))}
          
          {isLoggedIn ? (
            <button
              onClick={handleLogout}
              className="flex items-center gap-2 px-4 py-2 text-sm font-semibold text-primary hover:bg-primary/5 rounded-full transition-colors"
            >
              <LogOut size={18} strokeWidth={2.5} />
              <span className="hidden md:inline">Logout</span>
            </button>
          ) : (
            <button
              onClick={() => setShowLoginModal(true)}
              className="flex items-center gap-2 px-4 py-2 text-sm font-semibold text-primary hover:bg-primary/5 rounded-full transition-colors"
            >
              <LogIn size={18} strokeWidth={2.5} />
              <span className="hidden md:inline">Login</span>
            </button>
          )}
        </div>
      </div>

      {showLoginModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 max-w-md w-full mx-4">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-2xl font-bold">Owner Login</h2>
              <button
                onClick={() => setShowLoginModal(false)}
                className="text-gray-500 hover:text-gray-700"
              >
                âœ•
              </button>
            </div>
            <OwnerLogin onLoginSuccess={handleLoginSuccess} />
          </div>
        </div>
      )}
    </>
  )
} 